import axios from 'axios';
import request from "@/utils/request";

export default class MenuService {
	getMenus() {
		return request({
			url: 'http://localhost:10000/menu/list',
			method: 'get',
		})
		// return axios.get('http://localhost:10000/user/all').then(res => res.data.data);
    }
	saveMenu(data) {
		return request({
			url: 'http://localhost:10000/menu/save',
			method: 'post',
			data
		})
	}
	deleteMenu(data) {
		return request({
			url: 'http://localhost:10000/menu/delete',
			method: 'post',
			data
		})
	}
	deleteMenuBatch(data) {
		return request({
			url: 'http://localhost:10000/menu/deleteBatch',
			method: 'post',
			data
		})
	}
}
